<template>
  <header>
    <div id="fixed">
      <div class="container">
        <div class="header-container hidden-sm-only hidden-xs-only">
          <ul id="ul-menu">
            <li>
              <router-link to="/">{{t('header_menu.home')}}</router-link>
            </li>
            <li>
              <router-link to="/about">{{t('header_menu.about')}}</router-link>
            </li>
            <li>
              <router-link to="/road">{{t('header_menu.road')}}</router-link>
              <ul class="ul-secondary">
                <li><router-link to="/road/light">LIGHT</router-link></li>
                <li><router-link to="/road/lightdisc">LIGHT-DISC</router-link></li>
                <li><router-link to="/road/pro">PRO</router-link></li>
                <li><router-link to="/road/prodisc">PRO-DISC</router-link></li>
              </ul>
            </li>
            <li>
              <router-link to="/mountain/default">{{t('header_menu.mountain')}}</router-link>
            </li>
          </ul>
          <div id="language" @click="handleLanguage"></div>
        </div>
        <div class="header-container-mobile hidden-xl-only hidden-lg-only hidden-md-only">
          <i class="el-icon-s-unfold" @click="handleShowMenu"></i>
          <div class="header-menu-mask">
            <div class="header-menu-s">
              <ul id="ul-menu-s">
                <li>
                  <router-link to="/">{{t('header_menu.home')}}</router-link>
                </li>
                <li>
                  <router-link to="/about">{{t('header_menu.about')}}</router-link>
                </li>
                <li>
                  <router-link to="/road">{{t('header_menu.road')}}</router-link>
                  <ul class="ul-secondary-s">
                    <li><router-link to="/road/light">LIGHT</router-link></li>
                    <li><router-link to="/road/lightdisc">LIGHT-DISC</router-link></li>
                    <li><router-link to="/road/pro">PRO</router-link></li>
                    <li><router-link to="/road/prodisc">PRO-DISC</router-link></li>
                  </ul>
                </li>
                <li>
                  <router-link to="/mountain/default">{{t('header_menu.mountain')}}</router-link>
                </li>
                <li>
                  <a href="javascript:void(0);" @click="handleLanguage">{{showLanguage}}</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script setup>
import { onMounted, ref } from "@vue/runtime-core"
import { useI18n } from "vue-i18n"
import {useStore} from 'vuex'
const store = useStore()

let showLanguage = ref('')
const { t, locale } = useI18n()
const handleShowMenu = () => {
  let _ = document.querySelector(".header-menu-mask")
  _.style.left = "0%"
  _.onclick = () => {
    _.style.left = "-100%"
  }
  document.querySelector(".header-menu-s").onclick = (e) => {
    e.stopPropagation();
  }
}
const handleLanguage = () => {
  if (localStorage.getItem('language') === 'Cn') {
    localStorage.setItem('language', 'En')
    locale.value = 'En'
    showLanguage.value = '中文'
    store.commit('changeBannerEn')
    store.commit('changeLightEn')
  } else {
    localStorage.setItem('language', 'Cn')
    locale.value = 'Cn'
    showLanguage.value = 'ENGLISH'
    store.commit('changeBannerCn')
    store.commit('changeLightCn')
  }
}
onMounted(() => {
  if (!localStorage.getItem('language') || localStorage.getItem('language') === 'Cn') {
    localStorage.setItem('language', 'Cn')
    showLanguage.value = 'ENGLISH'
  } else {
    localStorage.setItem('language', 'En')
    showLanguage.value = '中文'
  }
})
</script>


<style lang="scss">
.header-menu-mask {
  position: fixed; left: -100%; top: 0px; z-index: 999; transition: left 0.3s;
  width: 100%; height: 100vh; background: rgba($color: #000000, $alpha: 0.3);
  .header-menu-s {
    width: 250px; height: 100%; background-color: #000000;
    background-image: url(../assets/images/logo-header.png); background-repeat: no-repeat;
    background-position: center 10px;
    #ul-menu-s {
      padding-top: 150px;
      >li {
        padding-left: 30px; padding-top: 30px;
        >a {
          font-size: 24px; color: #FFFFFF;
          &.router-link-active,&:hover {
            color: red !important;
          }
        }
        .ul-secondary-s {
          >li {
            padding-left: 30px; padding-top: 20px;
            >a {
              font-size: 22px; color: #FFFFFF;
              &.router-link-active,&:hover {
                border-bottom: 2px solid red;
              }
            }
          }
        }
      }
    }
  }
}
.header-container-mobile {
  i {color: #FFFFFF; font-size: 40px; padding-top: 100px; padding-left: 10px; cursor: pointer;}
}
</style>