<template>
  <section>
    <div class="container">
      <el-row :gutter="0">
        <el-col :span="24">
          <div id="videos">
            <video controls src="http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4" :style="'height:'+videoHeight+'px;'"></video>
          </div>
        </el-col>
      </el-row>
    </div>
  </section>
</template>

<script setup>
import {ref, onMounted} from 'vue'
const getVideoHeight = () => {
  let w = document.body.clientWidth
  if (w > 1600) {
    return 730
  } else if (w > 1200) {
    return (w - 300) * 730 / 1300
  } else {
    return (w - 100) * 730 / 1300
  }
}
let videoHeight = ref(getVideoHeight())
onMounted(() => {
  window.addEventListener('resize', () => {
    videoHeight.value = getVideoHeight()
  })
})
</script>

<style lang="scss">
#videos {
  padding: 10px 150px;
  @media (max-width: 768px) {padding: 10px 50px;}
  @media (min-width: 768px) {padding: 10px 50px;}
  @media (min-width: 992px) {padding: 10px 50px;}
  @media (min-width: 1200px) {padding: 10px 150px;}
  video {
    border: 1px solid #979797; background: #D8D8D8; width: 100%;
  }
}
</style>
